.hit-button-wrapper {
  position: relative;
  display: inline-block;
}

.hit-button {
  font-size: 14px;
  float: left;
  background-color: #0E75A7;
  border: none;
  padding: 12px 15px;
  -webkit-box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.3);
  color: #fff;
  cursor: pointer;
  width: 100%;
  height: 100%;
  -webkit-transition: background-color 0.1s, -webkit-transform 0.1s, -webkit-box-shadow 0.1s;
  transition: background-color 0.1s, -webkit-transform 0.1s, -webkit-box-shadow 0.1s;
  -o-transition: transform 0.1s, box-shadow 0.1s, background-color 0.1s;
  transition: transform 0.1s, box-shadow 0.1s, background-color 0.1s;
  transition: transform 0.1s, box-shadow 0.1s, background-color 0.1s, -webkit-transform 0.1s, -webkit-box-shadow 0.1s;
}
  .hit-button:hover:not([disabled]), .hit-button:focus:not([disabled]) {
    background-color: #167cAe;
    -webkit-transform: scale(1.02, 1.02);
        -ms-transform: scale(1.02, 1.02);
            transform: scale(1.02, 1.02);
    -webkit-box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.15);
            box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.15);
  }

  .hit-button:active:not([disabled]) {
    -webkit-transition: background-color 0.05s, -webkit-transform 0.05s, -webkit-box-shadow 0.05s;
    transition: background-color 0.05s, -webkit-transform 0.05s, -webkit-box-shadow 0.05s;
    -o-transition: transform 0.05s, box-shadow 0.05s, background-color 0.05s;
    transition: transform 0.05s, box-shadow 0.05s, background-color 0.05s;
    transition: transform 0.05s, box-shadow 0.05s, background-color 0.05s, -webkit-transform 0.05s, -webkit-box-shadow 0.05s;
    background-color: #0A71A3;
    -webkit-transform: scale(0.99, 0.99);
        -ms-transform: scale(0.99, 0.99);
            transform: scale(0.99, 0.99);
    -webkit-box-shadow: inset 2px 2px 3px 1px rgba(0, 0, 0, 0.3);
            box-shadow: inset 2px 2px 3px 1px rgba(0, 0, 0, 0.3)
  }
  .hit-button[disabled] {
    background-color: #bbb;
    color: #eee;
    cursor: not-allowed;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
  .hit-button.small {
    font-size: 12px;
    padding: 8px 10px;
  }



